import './bootstrap.min.css';
import './App.css';
import EmotionTable from './EmotionTable.js';
import React from 'react';
import axios from 'axios';

class App extends React.Component {
	state = {
		innercomp: (
			<textarea
				rows="4"
				cols="50"
				id="textinput"
				placeholder="enter your text here"
			/>
		),
		mode: 'text',
		sentimentOutput: [],
		sentiment: true,
	};

	renderTextArea = () => {
		document.getElementById('textinput').value = '';
		if (this.state.mode === 'url') {
			this.setState({
				innercomp: (
					<textarea
						rows="4"
						cols="50"
						id="textinput"
						placeholder="enter your text here"
					/>
				),
				mode: 'text',
				sentimentOutput: [],
				sentiment: true,
			});
		}
	};

	renderTextBox = () => {
		document.getElementById('textinput').value = '';
		if (this.state.mode === 'text') {
			this.setState({
				innercomp: (
					<textarea
						rows="3"
						cols="50"
						id="textinput"
						placeholder="enter your url here"
					/>
				),
				mode: 'url',
				sentimentOutput: [],
				sentiment: true,
			});
		}
	};

	sendForSentimentAnalysis = () => {
		this.setState({ sentiment: true });
		let ret = '';
		let url = '.';

		if (this.state.mode === 'url') {
			url = `/url/sentiment?url=${document.getElementById('textinput').value}`;
		} else {
			url = `/text/sentiment?text=${
				document.getElementById('textinput').value
			}`;
		}
		ret = axios.get('http://localhost:3001' + url);
		ret.then((response) => {
			console.log(response);
			this.setState({
				sentimentOutput: response.data.result.sentiment.document.score,
			});
			let output = response.data.result.sentiment.document.score;
			if (response.data.result.sentiment.document.label === 'positive') {
				output = (
					<div style={{ color: 'green', fontSize: 20 }}>
						{'sentiment: ' + response.data.result.sentiment.document.label}
					</div>
				);
			} else if (response.data.result.sentiment.document.label === 'negative') {
				output = (
					<div style={{ color: 'red', fontSize: 20 }}>
						{'sentiment: ' + response.data.result.sentiment.document.label}
					</div>
				);
			} else {
				output = (
					<div style={{ color: 'yellow', fontSize: 20 }}>
						{'sentiment: ' + response.data.result.sentiment.document.label}
					</div>
				);
			}
			this.setState({ sentimentOutput: output });
		});
	};

	sendForEmotionAnalysis = () => {
		this.setState({ sentiment: false });
		let ret = '';
		let url = '.';
		if (this.state.mode === 'url') {
			url = `/url/emotion?url=${document.getElementById('textinput').value}`;
		} else {
			url = `/text/emotion?text=${document.getElementById('textinput').value}`;
		}
		ret = axios.get('http://localhost:3001' + url);
		ret.then((response) => {
			this.setState({
				sentimentOutput: <EmotionTable emotions={response.data} />,
			});
		});
	};

	render() {
		return (
			<div className="App">
				<button
					className="btn btn-primary p-3 m-3"
					onClick={this.renderTextArea}
				>
					Text
				</button>
				<button className="btn btn-danger p-3 m-3" onClick={this.renderTextBox}>
					URL
				</button>
				<br />
				<br />
				{this.state.innercomp}
				<br />
				<button
					className="btn btn-primary p-3 m-3"
					onClick={this.sendForSentimentAnalysis}
				>
					Analyze Sentiment
				</button>
				<button
					className="btn btn-primary p-3 m-3"
					onClick={this.sendForEmotionAnalysis}
				>
					Analyze Emotion
				</button>
				<br />
				{this.state.sentimentOutput}
			</div>
		);
	}
}

export default App;
